import { Component, OnInit } from '@angular/core';
import { LocalDate } from 'js-joda';
import { share } from 'rxjs/operators';
import { AuthService } from 'src/app/auth/auth.service';
import { ApiService } from 'src/app/shared/api.service';

@Component({
  selector: 'app-eodreport',
  templateUrl: './eodreport.component.html',
  styleUrls: ['./eodreport.component.css'],
})
export class EodreportComponent implements OnInit {
  outlets;

  form = {
    outletname: undefined,
    startdate: LocalDate.now().toString(),
    enddate: LocalDate.now().toString(),
  };

  totalsales$;
  totalorders$;
  totalproducts$;
  orders$;
  orderitems$;

  constructor(private auth: AuthService, private api: ApiService) {}

  async ngOnInit() {
    // this.outlets = await this.api.get(`/api3/outlets`).toPromise();
    this.outlets = this.auth.getAccount().outlets;
  }

  generate(form) {
    this.totalsales$ = this.api
      .post(`/api3/eodreport/totalsales`, form)
      .pipe(share());
    this.totalorders$ = this.api
      .post(`/api3/eodreport/totalorders`, form)
      .pipe(share());
    this.totalproducts$ = this.api
      .post(`/api3/eodreport/totalproducts`, form)
      .pipe(share());
    this.orders$ = this.api.post(`/api3/eodreport/orders`, form).pipe(share());
    this.orderitems$ = this.api
      .post(`/api3/eodreport/orderitems`, form)
      .pipe(share());
  }
}
